import {CSSProperties, useEffect, useRef, useState} from "react"

interface DrawerProps extends React.HTMLAttributes<HTMLDivElement> {
    closed?: boolean | undefined
}

const Drawer: React.FC<DrawerProps> = props => {
    const {closed: input_closed = false, style, children, ...otherProps} = props
    const [closed, setClosed] = useState(input_closed)
    const drawerRef = useRef<HTMLDivElement | null>(null)

    const defaultStyle: React.CSSProperties = {
        overflow: "hidden",
        transitionDuration: "0.5s",
        transitionTimingFunction: "cubic-bezier(0.645, 0.045, 0.355, 1)",
    }

    const closedStyle: React.CSSProperties = {
        height: "0px",
    }

    const close = () => {
        if (!drawerRef.current) return
        drawerRef.current.style.height = drawerRef.current.scrollHeight + "px"
        void drawerRef.current.offsetHeight
        drawerRef.current.ontransitionend = () => {
            if (!drawerRef.current) return
            drawerRef.current.style.height = ""
            drawerRef.current.ontransitionend = null
            setClosed(true)
        }
        drawerRef.current.style.height = "0px"
    }

    const open = () => {
        if (!drawerRef.current) return
        drawerRef.current.ontransitionend = () => {
            if (!drawerRef.current) return
            drawerRef.current.style.removeProperty("height")
            drawerRef.current.ontransitionend = null
            setClosed(false)
        }
        drawerRef.current.style.height = drawerRef.current.scrollHeight + "px"
    }

    useEffect(() => {
        if (input_closed) {
            close()
        } else {
            open()
        }
    }, [input_closed])

    return (
        <div {...otherProps} style={{...defaultStyle, ...(closed ? closedStyle : {}), ...style}} ref={drawerRef}>
            {children}
        </div>
    )
}

export default Drawer
