import {HTMLAttributes} from "react"
import {classes} from "~/utilities/classes"
import "./Gap.css"

interface GapProps extends React.HTMLAttributes<HTMLDivElement> {
    centered?: boolean | undefined
}

const Gap: React.FC<GapProps> = props => {
    const {className, centered = false, ...otherProps} = props

    return <div className={classes("gap-q2z3", centered ? "centered-q2z3" : "", className)} {...otherProps} />
}

export default Gap
