import {useState} from "react"
import ChevronUpIcon from "~/icons/ChevronUpIcon"
import {classes} from "~/utilities/classes"
import Drawer from "~/components/UI/Drawer/Drawer"
import "./Search.css"

interface SearchProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    title?: string | null | undefined
    startClosed?: boolean | undefined
}

const Search: React.FC<SearchProps> = props => {
    const {className, title, startClosed = false, children, ...otherProps} = props
    const [closed, setClosed] = useState(startClosed)

    return (
        <div className={classes("search-k5ec", closed ? "closed-k5ec" : "", className)} {...otherProps}>
            <button className="button-k5ec" onClick={e => setClosed(state => !state)}>
                <ChevronUpIcon className="icon-k5ec" />
                <span className="title-k5ec">{title}</span>
            </button>
            <Drawer closed={closed} className="content-k5ec">
                {children}
            </Drawer>
        </div>
    )
}

export default Search
